import React from 'react';
import { Link } from 'react-router-dom';

const SideNav = props => {
		return(
		 <ul className={`sideNav ${props.toggle}`}>
		   <i className="material-more-options-icon material-icons md-24 md-36" onClick={props.onClick}>more_horiz</i>
           <li><Link to='/'><i className="material-home-icon material-icons md-24 md-36">home</i>Home</Link></li>
           <li><Link to='/about'><i className="material-face-icon material-icons md-24 md-36">face</i>About</Link></li>
           <li><Link to='/portfolio'><i className="material-assignment-icon material-icons md-24 md-36">assignment</i>Projects</Link></li>
           <li><Link to='/contact'><i className="material-email-icon material-icons md-24 md-36">email</i>Contact</Link></li>
         </ul>
		);
	}

export default SideNav;




